<template>
  <div>
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card" shadow="never">
      <div class="select-wrap">
        <el-row :gutter="10">
          <el-col :span="3">
            <el-select v-model="select" placeholder="" size="small">
              <el-option value=""></el-option>
            </el-select>
          </el-col>
          <el-col :span="5">
            <el-input v-model="queryInfo.query" placeholder="" size="small" clearable @clear="getUserList"></el-input>
          </el-col>
          <el-col :span="2">
            <el-button type="primary" icon="el-icon-search" size="small" @click="getUserList"></el-button>
          </el-col>
        </el-row>
      </div>
      <div class="table-wrap">
        <el-table
          :data="userList"
          style="width:100%"
          border
          size="small"
          :header-cell-style="{
            'background': 'rgb(245,250,247)',
            'text-align': 'center'
          }"
          :cell-style="{
            'text-align': 'center'
          }"
        >
          <el-table-column type="index" label="#"></el-table-column>
          <el-table-column
            prop="_id"
            label="id"
          >
          </el-table-column>
          <el-table-column
            prop="name"
            label=""
          >
          </el-table-column>
          <el-table-column
            label=""
          >
            <template slot-scope="scope">
              <el-button type="danger" icon="el-icon-delete" size="mini" @click="RemoveContentById(scope.row._id)"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <div class="page-wrap">
        <el-pagination
          background
           @current-change="getUserList()"
          :current-page.sync="queryInfo.page"
          layout="total, prev, pager, next"
          :page-size="queryInfo.pagesize"
          :total="totalData">
        </el-pagination>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    return {
      searchUserName: '',
      select: '',
      queryInfo: {
        query: '',
        page: 1,
        pagesize: 10
      },
      userList: [],
      totalData: 0
    }
  },
  created () {
    this.getUserList()
  },
  methods: {
    async getUserList () {
      const { data: res } = await this.$http.get('admin/category', { params: this.queryInfo })
      if (res.meta.code === 0) {
        this.userList = res.data.dataList
        this.totalData = res.data.total
      } else {
        this.$message.error('')
      }
    },
    async RemoveContentById (id) {
      console.log(id)
      const res = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(
        err => err //  'cancel'
      )
      // confirm
      if (res === 'confirm') {
        const { data: result } = await this.$http.get('/admin/category/delete', {
          params: {
            categoryId: id
          }
        })
        console.log(result)
        if (result.code === 0) {
          this.getUserList()
          this.$message.success(result.message)
        }
      } else {
        this.$message({
          type: 'info',
          message: ''
        })
      }
    }
  }
}
</script>
<style lang="less" scoped>
.el-table{
  margin-top: 20px;
}
.page-wrap{
  margin: 10px 0px;
  min-height: 28px;
  position: relative;
}
.el-pagination{
  position: absolute;
  left: 50%;
  transform: translate(-50%);
}
</style>
